import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';

// import { PushAlert } from '../Bracael';

import ComponentModalUpImage from './ComponentModalUpImage';
import ComponentMapThumb from './ComponentMapThumb';

import { Container, Main, Up } from '../styles/Thumb';

const { jQuery: $ } = window;

function ComponentEditThumb({ usePost, setPost }) {

  const [useModal, setModal] = useState(false);
  const [useModalThumb, setModalThumb] = useState(false);

  const useComponent = (() => {
    const Root = document.createElement('div');
    Root.setAttribute('role', 'modal');
    return Root;
  })();

  const { thumb = [] } = usePost;
  
  const altSendThumb = () => setModal(true);

  useEffect(() => {
    if (useModal) {
      ReactDOM.render(<ComponentModalUpImage {...{ usePost, setPost, useModal, setModal, useModalThumb, setModalThumb }} />, document.body.appendChild(useComponent));
    }
    else {
      if (useModalThumb) {
        $('[role="modal"]').fadeOut(300, function () { this.remove(); });
        setModalThumb(false);
      };
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useModal, useModalThumb]);

  return (
    <Container>
      <Main>
        {thumb.length ?
          thumb.map((data, i) => <ComponentMapThumb key={i} {...{ data, i, usePost, setPost }} />) : null}
        {thumb.length !== 8 ?
          <Up type="button" onClick={() => altSendThumb()}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 341.4 341.4" width="32" height="32" fill="#999"><polygon points="192,149.4 192,0 149.4,0 149.4,149.4 0,149.4 0,192 149.4,192 149.4,341.4 192,341.4 192,192 341.4,192 341.4,149.4" /></svg>
          </Up> : null}
      </Main>
    </Container>
  )
}

export default ComponentEditThumb;