import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, usePost, setPost, useStateNumber, setStateNumber }) {

  return (
    <ElementContext.Provider value={{ usePost, setPost, useStateNumber, setStateNumber }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotPost() {
  const context = useContext(ElementContext)
  const { usePost, setPost } = context;
  return { usePost, setPost };
}

export function SnapshotStateNumber() {
  const context = useContext(ElementContext)
  const { useStateNumber, setStateNumber } = context;
  return { useStateNumber, setStateNumber };
}
