import React from 'react';

import Bracael from '../Bracael';

import { Content, Title, Main, Select, Option } from '../styles/RingNumber';

function ComponentEditRingNumber({ usePost, setPost }) {

  // eslint-disable-next-line no-unused-vars
  const { size_ring = '' } = usePost;

  const altChange = (val) => setPost((prevState) => {
    return {
      ...prevState,
      size_ring: val
    }
  })

  return (
    <Content>
      <Title>Numerao de produto</Title>
      <Main>
        <Select onChange={(e) => altChange(e.target.value)} value={size_ring}>
          {Bracael.size_ring.map((data, i) => (
            <Option key={i}>{data}</Option>
          ))}
        </Select>
      </Main>
    </Content>
  )
}

export default ComponentEditRingNumber;
