import styled from "styled-components";

import { BorderRadius } from "./root";

export const Content = styled.div`
  flex-direction: column;
  display: flex;
  height: 100%;
  min-height: 46vh;

  & > *:not(:last-child) {
    margin-bottom: 2rem;
  }
`

export const Footer = styled.div`
  width: 100%;
  display: flex;
`

export const Button = styled.button`
  width: 100%;
  color: #fff;
  background-color: #f33;
  box-shadow: 0 0 8px -3px #000;
  white-space: nowrap;
  padding: 1rem 2rem;
  margin: auto;

  ${BorderRadius(3)}
`

export const Mainly = styled.div`
  height: 100%;
`

export const Main = styled.div`
  flex-direction: column;
  display: flex;
  height: 100%;

  & > *:not(:last-child) {
    margin-bottom: 1rem;
  }
`

export const Group = styled.div`
  display: flex;
  width: ${({ useWidth }) => useWidth && '100%'};

  & > *:not(:last-child) {
    margin-right: 1.6rem;
  }
`

export const Send = styled(Button)``

export const Empty = styled.div`
  justify-content: center;
  flex-direction: column;
  align-items: center;
  display: flex;
  height: 100%;

  & > *:not(:last-child) {
    margin-bottom: 1.8rem;
  }

  & span {
    font-size: 2rem;
  }
`
