import styled from "styled-components";

import { BorderRadius } from './root';

export const Container = styled.div`
  flex-direction: column;
  display: flex;

  & > *:not(:last-child) {
    margin-bottom: 1.6rem;
  }
`

export const Main = styled.div`
  display: table;

  & > *:not(:nth-last-child(1)) {
    margin-bottom: 1.6rem;
  }

  & > *:not(:nth-child(4)):not(:nth-child(8)) {
    margin-right: 1.6rem;
  }
`

export const Up = styled.button`
  width: 100%;
  max-width: 21%;
  min-height: 72px;
  min-width: 72px;
  max-height: 72px;
  height: 100%;
  margin: auto 0;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  display: flex;
  background-size: cover;
  background-position: center center;
  background-color: rgb(232, 232, 232);
  position: relative;
  float: left;

  ${BorderRadius(3)}

  & svg {
    float: left;
  }
`

export const Content = styled.div`
  width: 100%;
  max-width: 21%;
  min-width: 72px;
  min-height: 72px;
  max-height: 72px;
  height: 100%;
  margin: auto 0;
  overflow: hidden;
  display: flex;
  align-items: center;
  background-size: cover;
  background-position: center center;
  background-color: rgb(232, 232, 232);
  position: relative;
  float: left;

  ${BorderRadius(3)}

  & img {
    float: left;
  }
`

export const Delete = styled.button`
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 0.8rem;
  padding: 0.8rem;
  position: absolute;
  top: 0;
  right: 0;

  & svg {
    float: left;
    z-index: 1;
  }

  &:after {
    content: "";
    width: 62px;
    position: absolute;
    height: 62px;
    z-index: 0;
    background: -moz-radial-gradient(center, ellipse cover,  rgba(0,0,0,0.25) 0%, rgba(0,0,0,0.25) 22%, rgba(0,0,0,0) 71%, rgba(0,0,0,0) 100%); /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover,  rgba(0,0,0,0.25) 0%,rgba(0,0,0,0.25) 22%,rgba(0,0,0,0) 71%,rgba(0,0,0,0) 100%); /* Chrome10-25,Safari5.1-6 */
    background: radial-gradient(ellipse at center,  rgba(0,0,0,0.25) 0%,rgba(0,0,0,0.25) 22%,rgba(0,0,0,0) 71%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#40000000', endColorstr='#00000000',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    top: -26px;
    right: -20px;
  }
`
