import styled from 'styled-components';

// import { BorderRadius } from './root';

export const Head = styled.div`
  display: flex;

  @media (max-width: 720px) {
    margin-bottom: initial;
    box-shadow: rgba(0, 0, 0, 0.25) 0 0 16px;
  }

  & ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    margin: 0 auto;
  }

  & > * {
    width: 100%;
  }
`

export const Radio = styled.li`
  position: relative;

  & input:checked ~ label::after {
    background-color: #cc0000;
    width: 100%;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    left: 0;
    right: 0;
  }

  & input:not(:checked) ~ label:hover::after {
    background-color: rgba(170, 170, 170, 0.45);
    width: 60%;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    left: 20%;
    right: 20%;
  }

  & label {
    color: hsl(0, 0%, 60%);
    max-width: 150px;
    padding: 1.6rem;
    float: left;
    width: 100%;
    cursor: pointer;
    text-align: center;
    transition: color .3s;
    font-weight: 500;
    font-family: var(--font-poppins);
    font-size: 1.6rem;
    
    &:hover {
      color: #404040;
    }

    &::after {
      content: "";
      display: block;
      width: 20%;
      height: 3px;
      left: 40%;
      right: 40%;
      transition: 0.3s;
      background-color: transparent;
      position: absolute;
      bottom: 0;
    }
  }
`

export const Main = styled.div`
  width: 200%;
  position: relative;
  transition: .3s;
  display: flex;
  height: 100%;
  left: ${({ useScreen }) => useScreen === 1 ? '0' : '-100%'};
  max-height: 86%;

  & > div {

    &:nth-child(1) {
      opacity: ${({ useScreen }) => useScreen === 1 ? '1' : '0'};
    }

    &:nth-child(2) {
      opacity: ${({ useScreen }) => useScreen === 1 ? '0' : '1'};
    }
  }
`

export const Table = styled.div`
  width: 100%;
  padding: 2.6rem 2rem;
  transition: opacity 1.5s ease 0s;
  overflow-x: hidden;
  overflow-y: overlay;

  @media (min-width: 1040px) {
    padding: 4.2rem 6.6rem;
  }

  & > small {
    color: #aaa;
    align-items: center;
    justify-content: center;
    display: flex;
    height: 100%;
  }
`
