import React from 'react';
import ReactDOM from 'react-dom';

import ComponentModalSimple from './ComponentModalSimple';

import { Content } from '../styles/ThumbMap';

// HELP remove o formato da string de nome da imagem
const ComaRemove = (val) => {
  const nameSplit = [...val.split('.')];
  nameSplit.pop();
  return nameSplit.join();
}

function ComponentMapImageAllDelete({ data, useDeleteImg, setDeleteImg }) {

  const createDomModal = (() => {
    const Root = document.createElement('div')
    Root.setAttribute('role', 'modaly');
    return Root;
  })();

  // eslint-disable-next-line no-unused-vars
  const { name, size } = data;

  const altSelectImage = () => Outward({
    title: 'Ateno!',
    message: 'Ei, voc quer mesmo remover essa imagem?',
    button: ['Cancelar', 'Sim, remover!'],
    buttonColor: '#ff2222',
    action: function() {
      setDeleteImg(name);
    }
  });

  const Outward = data => ReactDOM.render(<ComponentModalSimple {...data} />, document.body.appendChild(createDomModal));

  return (
    <Content style={{ backgroundImage: "url(https://firebasestorage.googleapis.com/v0/b/ensollarada.appspot.com/o/products%2Fimages%2Fthumbs%2F" + ComaRemove(name) + "_160x160.jpeg?alt=media)" }} onClick={() => altSelectImage()}>
      <img src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEAAAEALAAAAAABAAEAgM7t3M7t3AICTAEAOw==" height="100%" width="100%" alt="" />
    </Content>
  );
}

export default ComponentMapImageAllDelete;