import React from 'react';

import ComponentMapCardAttendance from './ComponentMapCardAttendance';

import { Content, Cliente } from '../styles/MapCard';

function ComponentMapAttendance({ data, i }) {

  const uid = data[0];

  console.log(uid);

  return (
    <Content>
      <Cliente>{data[0]}</Cliente>
      {Object.entries(data[1])
        .map((data, i) => <ComponentMapCardAttendance {...{ data, i, uid }} key={data[0]} />)}
    </Content>
  );
}

export default ComponentMapAttendance;