import React from 'react';

import { Li, Input, Label } from '../styles/SizeLetter';

function ComponentMapSizeLetter({ data, usePost, setPost }) {

  const { size_letter = {} } = usePost;
  const value = size_letter[data] ? size_letter[data] : '';

  const CambioInput = (val) => {
    if (!isNaN(Number(val)) && val.replace(/[^0-9]/g, '') !== String(size_letter[data]) && size_letter[data] !== Number(val)) {
      setPost((prevState) => {
        const { size_letter = {} } = prevState;
          return {
          ...usePost,
          size_letter: {
            ...size_letter,
            [data] : Number(val)
          }
        };
      });
    };
  };

  return (
    <Li>
      <Label>{data}</Label>
      <Input type="text" placeholder="0"
        onInput={(e) => CambioInput(e.target.value)} autoCapitalize="on" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off"
        value={value} />
    </Li>
  )
}

export default ComponentMapSizeLetter;
