import React from 'react';
import { useAsync } from "react-async";

import { functions } from '../utils/Connection';

import ComponentCardAttendance from '../components/ComponentCardAttendance';

const fetchPerson = async ({ signal }) => {

  const responseFirestore = await functions()
  .httpsCallable('getItemConnection')('order')
  .then((snapshot) => snapshot.data)

  return responseFirestore;
}

function Service() {

  // eslint-disable-next-line no-unused-vars
  const { data, error } = useAsync({ promiseFn: fetchPerson })

  if (error) return error.message
  if (data) return  <ComponentCardAttendance {...{ data }} />
  return <div>Carregando...</div>
}

export default Service;