import styled from "styled-components";

import { BorderRadius } from "./root";

export const Content = styled.div`
  display: flex;
  flex-direction: column;

  & > *:not(:last-child) {
    margin-bottom: 1.6rem;
  }
`

export const Cliente = styled.span`
  width: max-content;
  background: #fff;
  font-size: 1.5rem;
  padding: 0.6rem 1rem;
  box-shadow: 0px -1px rgb(0,0,0,0.05);

  ${BorderRadius(3)}
`
