import React from 'react';

import ComponentMapAttendance from './ComponentMapAttendance';

import { Container } from '../styles/Service';

function ComponentCardAttendance({ data }) {

  return (
    <Container>
      {Object.entries(data)
        .map((data, i) => <ComponentMapAttendance {...{ data, i }} key={data[0]} />)}
    </Container>
  )
}

export default ComponentCardAttendance;