import React, { useEffect, useState } from 'react';
import { formatDistanceToNow } from 'date-fns';
import pt from 'date-fns/locale/pt-BR';

import { Timestamp } from '../styles/CardAttendance';

function ComponentOrderAttendance({ date_approved }) {

  const [useTime, setTime] = useState(formatDistanceToNow(new Date(date_approved), { addSuffix: true, locale: pt, timeZone: 'America/Sao_Paulo' }))

  useEffect(() => {
    setInterval(() => {
      setTime(formatDistanceToNow(new Date(date_approved), { addSuffix: true, locale: pt }));
    }, 1000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useTime]);
  
  return (
    <Timestamp>{useTime}</Timestamp>
  )
}

export default ComponentOrderAttendance;