import React from 'react';

import { Li, Input, Label } from '../styles/SizeNumber';

function ComponentMapSizeNumber({ data, usePost, setPost }) {

  const { size_number = {} } = usePost;
  const value = size_number[data] ? size_number[data] : '';

  const CambioInput = (val) => {
    setPost((prevState) => {
      const { size_number = {} } = prevState;

      return {
        ...usePost,
        size_number: {
          ...size_number,
          [data] : Number(val)
        }
      };
    });
  }

  return (
    <Li>
      <Label>{data}</Label>
      <Input type="text" placeholder="0"
        onInput={(e) => CambioInput(e.target.value)} autoCapitalize="on" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off"
        value={value} />
    </Li>
  )
}

export default ComponentMapSizeNumber;