import React, { useState } from 'react';

import ComponentPostCategory from '../components/ComponentPostCategory';
import ComponentPostSubcategory from '../components/ComponentPostSubcategory';
import ComponentPostEdit from '../components/ComponentPostEdit';

// import { Container } from './styles';

function CreateProduct() {

  // eslint-disable-next-line no-unused-vars
  const [usePost, setPost] = useState({});
  // eslint-disable-next-line no-unused-vars
  const [useStage, setStage] = useState(1);

  console.log(usePost);
  
  if(useStage === 1) return <ComponentPostCategory {...{ useStage, setStage, usePost, setPost }} />;
  if(useStage === 2) return <ComponentPostSubcategory {...{ useStage, setStage, usePost, setPost }} />;
  if(useStage === 3) return <ComponentPostEdit {...{ usePost, setPost }} />;
  return <div>Erro!</div>;
}

export default CreateProduct;