import React from 'react';
import { useHistory } from 'react-router-dom';

import ComponentMapProduct from './ComponentMapProduct';

import { Content, Menu, List } from '../styles/Products';

function ComponentProductList({ data }) {

  const history = useHistory();

  const organize = (a, b) => (a[1].published < b[1].published) ? 1 : ((b[1].published < a[1].published) ? -1 : 0);

  return (
    <Content>
      <Menu>
        <button type="button" onClick={() => history.push('/post')}>
          <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14" fill="#fff"><path d="M14,8H8v6H6V8H0V6H6V0H8V6h6V8Z"></path></svg>
          <span>Novo produto</span>
        </button>
      </Menu>
      <List>
      {Object.keys(data).length ?
        Object.entries(data)
          .sort(organize)
          .map((data, i) => <ComponentMapProduct key={data[0]} {...{ data, i }} />) :
        <small>No h produtos publicado!</small>}
      </List>
    </Content>
  )
}

export default ComponentProductList;