import React from 'react';

import Bracael from '../Bracael';

import ComponentMapSizeNumber from './ComponentMapSizeNumber';

import { Content, Title, Main } from '../styles/SizeNumber';

function ComponentEditSizeNumber({ usePost, setPost }) {

  return (
    <Content>
      <Title>Produtos em estoque</Title>
      <Main>
        {Bracael.size_number.map((data, i) => <ComponentMapSizeNumber key={i} {...{ data, usePost, setPost }} />)}
      </Main>
    </Content>
  )
}

export default ComponentEditSizeNumber;