import styled from "styled-components";

import { BorderRadius } from './root';

export const Content = styled.div`
  color: rgb(153, 153, 153);
  font-size: 12px;
  box-shadow: rgb(204 204 204) 0px 0px 16px;
  background-color: rgb(255, 255, 255);
  overflow: hidden;

  ${BorderRadius(3)}
`

export const L = styled.div`
  padding: 1.2rem 1.6rem;
  flex-direction: column;
  display: flex;

  & > *:not(:last-child) {
    margin-bottom: 1.2rem;
  }
`

export const R = styled.div`
  background: #FAFAFC;
  border-top: 1px solid #EFEEF9;
  justify-content: space-between;

  @media (max-width: 1039px) {
    padding: 0.6rem 1.6rem 1rem !IMPORTANT;
    flex-direction: column !IMPORTANT;
    align-items: center !IMPORTANT;
    justify-content: initial !IMPORTANT;
  }
`

export const Timestamp = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: 1039px) {
    font-size: 1.3rem;
  }
`

export const Buttons = styled.div`
  justify-content: center;
  display: flex;

  @media (max-width: 1039px) {
    margin-bottom: 0.5rem;
  }

  & button {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 26px;
    padding: 0 1rem 2px;
    padding-right: 1.3rem;
    line-height: 27px;
    transition: .1s;
    background: #d8d8d8;
    box-shadow: inset 0px -2px 0px 0px rgba(0, 0, 0, 0.15);
    color: rgba(0, 0, 0, 0.5);
    font-weight: 500;

    @media (max-width: 1039px) {
      padding: 0 1rem;
      -webkit-box-sizing: initial;
      box-sizing: border-box;
      background: border-box;
      box-shadow: initial;
      border-radius: 50%;
      color: initial;
      height: 30px;
      width: 30px;

      & span {
        display: none;
      }

      &:hover {
        background: #888888 !IMPORTANT;

        & svg {
          fill: #ffffff;
        }
      }
    }

    @media (min-width: 721px) {
      ${BorderRadius(3)}
    }

    & span {
      margin-left: 1.2rem;
    }

    & svg {
      fill: #888888;
    }

    &:not(:last-child) {
      margin-right: 1rem;
    }
  }
`

export const Master = styled.div`
  display: flex;

  & > div {
    width: 100%;
  }
`

export const Left = styled.div`
  width: 100%;
  margin-right: 1.6rem;
  justify-content: space-between;
  flex-direction: column;
  display: flex;
`

export const Name = styled.span`
  font-size: 2.2rem;
  color: #323232;
  display: block;
`

export const Status = styled.button`
  width: 100%;
  color: #fff;
  background-color: #279acc;
  border-bottom: 2px solid #1682b5;
  height: 30px;
  padding: 2px 1.3rem 0 1.3rem;
  margin: 0 1rem;
  font-weight: 500;
  white-space: nowrap;

  ${BorderRadius(3)}

  @media (max-width: 1039px) {
    margin: initial;
    height: 26px;
    font-size: 1.4rem;
  }
`

export const Right = styled.div`
  max-width: 33%;

  @media (min-width: 1040px) {
    & > div:not(:last-child) {
      margin-bottom: 1.6rem;
    }
  }
`

export const Values = styled.div`
  display: flex;
`

export const Item = styled.div`
  color: #000;
  width: 100%;
  flex-direction: column;
  display: flex;
`

export const ItemChild = styled.span`
  font-weight: 300;
  font-size: 1.4rem;
`

export const Price = styled.span`
  font-size: 1.8rem;
  color: #000;
`
